import http from '../../http.js'
export default {
	data() {
		return {
			customerid: "",
			maskState: 0, //优惠券面板显示状态
			desc: '', //备注
			payType: 1, //1微信 2支付宝
			couponList: [{
					title: '新用户专享优惠券',
					price: 5,
				},
				{
					title: '庆五一发一波优惠券',
					price: 10,
				},
				{
					title: '优惠券优惠券优惠券优惠券',
					price: 15,
				}
			],
			addressDataDefault: {
				// receivername: '许小星',
				// mobile: '13853989563',
				// addressName: '金九大道',
				// address: '山东省济南市历城区',
				// area: '149号',
				// default: false,
			},
			addressinfo: {},
			product: {},
			categary: {},
			productImage: ""
		}
	},
	onLoad(option) {
		//商品数据
		//let data = JSON.parse(option.data);
		console.log("option product");

		const product = JSON.parse(decodeURIComponent(option.product));
		this.product = product;
		console.log(product);
		this.productImage = this.myroot + "upload/" + product.image;
		console.log("option item");
		const item = JSON.parse(decodeURIComponent(option.item));
		this.categary = item;
		console.log(item);
		//	console.log(">>>>" + getApp().globalData.hasLogin);

	},
	onShow() {
		if (!getApp().globalData.hasLogin) {
			uni.showModal({
				title: '提示',
				content: '你还没有登录',
				success: function(res) {
					if (res.confirm) {
						uni.navigateTo({
							url: "../public/login"
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		} else {
			this.getDefaultCustomerAddress();

		}

	},
	methods: {

		addressDetail() {
			uni.navigateTo({
				url: "/pages/address/address?c_id=" + this.customerid
			})
		},
		getDefaultCustomerAddress() {
			try {
				var customer = uni.getStorageSync("userInfo");
				//console.log(customer)
				this.customerid = customer.id;
				this.getDefaultCustomerAddressHttp(customer.id);
			} catch (e) {

			}
		},

		getDefaultCustomerAddressHttp(id) {
			uni.request({
				url: this.myroot + "getDefaultCustomerAddress/" + id,
				success: (res) => {
					if (res.data.code == -1) {
						this.addressDataDefault = {
							receivername: '暂无',
							mobile: '暂无',
							addressName: '暂无',
							address: '暂无',
							area: '暂无',
							default: false,
						}
					} else {
						//	console.log("...............")
						//console.log(res.data.result)
						this.addressDataDefault = res.data.result;
					}
				}
			})
		},
		//显示优惠券面板
		toggleMask(type) {
			let timer = type === 'show' ? 10 : 300;
			let state = type === 'show' ? 1 : 0;
			this.maskState = 2;
			setTimeout(() => {
				this.maskState = state;
			}, timer)
		},
		numberChange(data) {
			this.number = data.number;
		},
		changePayType(type) {
			this.payType = type;
		},
		submit: async function() {

			var order = {
				"createTime": this.$api.dateFormat("YYYY-mm-dd HH:MM:SS", new Date()),
				"p_id": this.product.id,
				"s1_id": this.categary[0].id,
				"s2_id": this.categary[1].id,
				"state": 1,
				"customer_id": this.customerid
			}
			console.log(">>>>>>>>>>>");
			uni.request({
				url: this.myroot + "CreateOrder",
				data: order,
				dataType: "json",
				method: "POST",
				success: (res) => {
					if (res.data.code == 0) {
						uni.navigateTo({
							url: "../money/pay"
						})
					}
				},
				fail: (err) => {
					reject('err')
				}
			});

		},
		stopPrevent() {}
	}
}
